import React, { Component } from "react";
import InputForm from "./InputForm";
import SearchTable from "./SearchTable";

class HomeForm extends Component {
  constructor() {
    super();
    let varia = [];
    let nw = new Date();
    let month = nw.getMonth() + 1;
    let day = nw.getDate();
    let year = nw.getFullYear();
    let newdate = year + "-" + month + "-" + day;
    if (JSON.parse(localStorage.getItem("items")) != null) {
      varia = JSON.parse(localStorage.getItem("items"));
    }
    this.state = {
      billno: "",
      category: "",
      weight: "",
      location: "",
      price: "",
      tdate: String(newdate),
      items: varia,
      headers: [
        { label: "Bill Number", key: "billno" },
        { label: "Category", key: "category" },
        { label: "Weight", key: "weight" },
        { label: "Location", key: "location" },
        { label: "Price", key: "price" },
        { label: "Date", key: "tdate" },
      ],
    };
    console.log(JSON.parse(localStorage.getItem("items")));
    this.handleScan = this.handleScan.bind(this)
  }
  handleFormSubmit = (e) => {
    e.preventDefault();

    let items = [...this.state.items];

    items.push({
      billno: this.state.billno,
      category: this.state.category,
      weight: this.state.weight,
      location: this.state.location,
      price: this.state.price,
      tdate: this.state.tdate,
    });
    this.setState(
      {
        items,
        billno: "",
        category: "",
        weight: "",
        location: "",
        price: "",
      },
      () => {
        localStorage.setItem("items", JSON.stringify(this.state.items));
      }
    );
    console.log(this.state.items);
  };

  handleScan(data){
    this.setState({
      inputBillNo: data,
    })

    let strFirstFour = data.substring(0, 4);
      if (strFirstFour == "1600") {
        this.setState({
          category: "DS",
        });
      } else if (strFirstFour == "5809") {
        this.setState({
          category: "AS",
        });
      } else if (strFirstFour == "5072") {
        this.setState({
          category: "ES",
        });
      }
  }


  handleInputChange = (e) => {
    let input = e.target;
    let name = e.target.name;
    let value = input.value;
    if (name == "billno") {
      let strFirstFour = value.substring(0, 4);
      if (strFirstFour == "1600") {
        this.setState({
          category: "DS",
        });
      } else if (strFirstFour == "5809") {
        this.setState({
          category: "AS",
        });
      } else if (strFirstFour == "5072") {
        this.setState({
          category: "ES",
        });
      }
    }
    this.setState({
      [name]: value,
    });
  };

  handleDate = (e) => {
    let ISTTime = e.target.value;
    let month = ISTTime.getMonth() + 1;
    let day = ISTTime.getDate();
    let year = ISTTime.getFullYear();
    let newdate = year + "-" + month + "-" + day;
    this.setState({
      tdate: String(newdate),
    });
  };
  Greeting() {
    const isLoggedIn = this.state.items;
    if (isLoggedIn.length == 0) {
      return JSON.parse(localStorage.getItem("items"));
    }
    return this.state.items;
  }
  render() {
    return (
      <div className="App">
        <InputForm
          handleFormSubmit={this.handleFormSubmit}
          handleInputChange={this.handleInputChange}
          inputBillNo={this.state.billno}
          inputCategory={this.state.category}
          inputWeight={this.state.weight}
          inputLocation={this.state.location}
          inputPrice={this.state.price}
          inputDate={this.state.tdate}
          handleDate={this.handleDate}
        />
        <br></br>
        <SearchTable items={this.Greeting()}></SearchTable>
      </div>
    );
  }
}

export default HomeForm;
