import "primeicons/primeicons.css";
import "primereact/resources/themes/saga-blue/theme.css";
import "primereact/resources/primereact.css";
import "primeflex/primeflex.css";
import "../index.css";

import React, { Component } from "react";
import { DataTable } from "primereact/datatable";
import { Column } from "primereact/column";
import { Button } from "primereact/button";
import { Ripple } from "primereact/ripple";
import { Dropdown } from "primereact/dropdown";
import { InputText } from "primereact/inputtext";
import { CustomerService } from "../service/CustomerService";
import { classNames } from "primereact/utils";

class AnalyticsTable extends Component {
	constructor(props) {
		super(props);

		this.state = {
			customers2: [],
			first1: 0,
			rows1: 10,
			first2: 0,
			rows2: 10,
			currentPage: 1,
			pageInputTooltip: "Press 'Enter' key to go to this page.",
		};

		this.customerService = new CustomerService();

		this.onCustomPage1 = this.onCustomPage1.bind(this);
		this.onPageInputKeyDown = this.onPageInputKeyDown.bind(this);
		this.onPageInputChange = this.onPageInputChange.bind(this);
	}

	onCustomPage1(event) {
		this.setState({
			first1: event.first,
			rows1: event.rows,
			currentPage: event.page + 1,
		});
	}

	onPageInputKeyDown(event, options) {
		if (event.key === "Enter") {
			const page = parseInt(this.state.currentPage);
			if (page < 0 || page > options.totalPages) {
				this.setState({
					pageInputTooltip: `Value must be between 1 and ${options.totalPages}.`,
				});
			} else {
				const first = this.state.currentPage ? options.rows * (page - 1) : 0;

				this.setState({
					first1: first,
					pageInputTooltip: "Press 'Enter' key to go to this page.",
				});
			}
		}
	}

	onPageInputChange(event) {
		this.setState({ currentPage: event.target.value });
	}

	componentDidMount() {
	
		}

	render() {
		const paginatorLeft = (
			<Button type="button" icon="pi pi-refresh" className="p-button-text" />
		);
		const paginatorRight = (
			<Button type="button" icon="pi pi-cloud" className="p-button-text" />
		);
		const template1 = {
			layout:
				"PrevPageLink PageLinks NextPageLink RowsPerPageDropdown CurrentPageReport",
			PrevPageLink: (options) => {
				return (
					<button
						type="button"
						className={options.className}
						onClick={options.onClick}
						disabled={options.disabled}
					>
						<span className="p-p-3">Previous</span>
						<Ripple />
					</button>
				);
			},
			NextPageLink: (options) => {
				return (
					<button
						type="button"
						className={options.className}
						onClick={options.onClick}
						disabled={options.disabled}
					>
						<span className="p-p-3">Next</span>
						<Ripple />
					</button>
				);
			},
			PageLinks: (options) => {
				if (
					(options.view.startPage === options.page &&
						options.view.startPage !== 0) ||
					(options.view.endPage === options.page &&
						options.page + 1 !== options.totalPages)
				) {
					const className = classNames(options.className, {
						"p-disabled": true,
					});

					return (
						<span className={className} style={{ userSelect: "none" }}>
							...
						</span>
					);
				}

				return (
					<button
						type="button"
						className={options.className}
						onClick={options.onClick}
					>
						{options.page + 1}
						<Ripple />
					</button>
				);
			},
			RowsPerPageDropdown: (options) => {
				const dropdownOptions = [
					{ label: 10, value: 10 },
					{ label: 20, value: 20 },
					{ label: 50, value: 50 },
					{ label: "All", value: options.totalRecords },
				];

				return (
					<Dropdown
						value={options.value}
						options={dropdownOptions}
						onChange={options.onChange}
						appendTo={document.body}
					/>
				);
			},
			CurrentPageReport: (options) => {
				return (
					<span
						className="p-mx-3"
						style={{ color: "var(--text-color)", userSelect: "none" }}
					>
						Go to{" "}
						<InputText
							size="2"
							className="p-ml-1"
							value={this.state.currentPage}
							tooltip={this.state.pageInputTooltip}
							onKeyDown={(e) => this.onPageInputKeyDown(e, options)}
							onChange={this.onPageInputChange}
						/>
					</span>
				);
			},
		};
		return (
			
			<div>
				<div className="card">
					<h5>Monthly View</h5>
					<DataTable
						value={JSON.parse(this.props.dataFromParent)}
						paginator
						paginatorTemplate={template1}
						first={this.state.first1}
						rows={this.state.rows1}
						onPage={this.onCustomPage1}
					>
						<Column field="CAWBNO" header="Bill Number"></Column>
						<Column field="CPRODCODE" header="Category"></Column>
						<Column field="NACTWGT" header="Actual Weight"></Column>
						<Column field="NCHRGWT" header="Charge Weight"></Column>
						<Column field="NTOTALAMT" header="Amount"></Column>
						<Column field="BOOKING_DATE" header="MHS Date"></Column>
						<Column field="PARCEL_RATE" header="MHS Amount"></Column>
						<Column field="PLACE" header="MHS Place"></Column>
						<Column field="PARCEL_WEIGHT" header="MHS Weight"></Column>
						<Column field="Profit" header="MHS Profit"></Column>

					</DataTable>
				</div>
			</div>
		);
	}
}

export default AnalyticsTable;
