import "primeicons/primeicons.css";
import "primereact/resources/themes/saga-blue/theme.css";
import "primereact/resources/primereact.css";
import "primeflex/primeflex.css";
import "../index.css";
import ReactDOM from "react-dom";

import React, { Component } from "react";
import { classNames } from "primereact/utils";
import { Calendar } from "primereact/calendar";
import { DataTable } from "primereact/datatable";
import { Column } from "primereact/column";
import { Toast } from "primereact/toast";
import { Button } from "primereact/button";
import { Toolbar } from "primereact/toolbar";
import { RadioButton } from "primereact/radiobutton";
import { InputNumber } from "primereact/inputnumber";
import { Dialog } from "primereact/dialog";
import { InputText } from "primereact/inputtext";

class SearchTable extends Component {
  emptyProduct = {
    billno: null,
    category: null,
    weight: 0,
    location: "",
    price: 0,
    tdate: "",
  };

  constructor(props) {
    super(props);

    this.state = {
      products: this.props.items,
      productDialog: false,
      deleteProductDialog: false,
      deleteProductsDialog: false,
      product: this.emptyProduct,
      submitted: false,
      globalFilter: null,
    };

    this.cols = [
      { field: "billno", header: "Bill Number" },
      { field: "category", header: "Category" },
      { field: "weight", header: "Weight" },
      { field: "location", header: "Location" },
      { field: "price", header: "Price" },
      { field: "tdate", header: "Date" },
    ];

    this.exportColumns = this.cols.map((col) => ({
      title: col.header,
      dataKey: col.field,
    }));

    this.rightToolbarTemplate = this.rightToolbarTemplate.bind(this);
    this.priceBodyTemplate = this.priceBodyTemplate.bind(this);
    this.actionBodyTemplate = this.actionBodyTemplate.bind(this);

    this.hideDialog = this.hideDialog.bind(this);
    this.saveProduct = this.saveProduct.bind(this);
    this.editProduct = this.editProduct.bind(this);
    this.confirmDeleteProduct = this.confirmDeleteProduct.bind(this);
    this.deleteProduct = this.deleteProduct.bind(this);
    this.exportCSV = this.exportCSV.bind(this);
    this.exportPdf = this.exportPdf.bind(this);
    this.deleteSelectedProducts = this.deleteSelectedProducts.bind(this);
    this.onCategoryChange = this.onCategoryChange.bind(this);
    this.onInputChange = this.onInputChange.bind(this);
    this.onInputNumberChange = this.onInputNumberChange.bind(this);
    this.hideDeleteProductDialog = this.hideDeleteProductDialog.bind(this);
    this.hideDeleteProductsDialog = this.hideDeleteProductsDialog.bind(this);
  }
  componentDidUpdate(prevProps) {
    if (prevProps.items !== this.props.items) {
      this.setState({ products: this.props.items });
    }
  }
  printjson() {
    console.log(this.props.items);
    const requestOptions = {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify(this.props.items),
    };
    fetch("http://127.0.0.1:5000/save_data", requestOptions)
      .then((response) => console.log(response.json()))
      .then((data) => {
        console.log(data);
        localStorage.clear();
      });
  }

  formatCurrency(value) {
    return value.toLocaleString("en-US", {
      style: "currency",
      currency: "INR",
    });
  }

  hideDialog() {
    this.setState({
      submitted: false,
      productDialog: false,
    });
  }

  hideDeleteProductDialog() {
    this.setState({ deleteProductDialog: false });
  }

  hideDeleteProductsDialog() {
    this.setState({ deleteProductsDialog: false });
  }

  saveProduct() {
    let state = { submitted: true };
    let products = [...this.state.products];
    let product = { ...this.state.product };
    if (this.state.product.billno) {
      const index = this.findIndexById(this.state.product.billno);

      products[index] = product;
      this.toast.show({
        severity: "success",
        summary: "Successful",
        detail: "Product Updated",
        life: 3000,
      });
      state = {
        ...state,
        products,
        productDialog: false,
        product: this.emptyProduct,
      };
    }

    this.setState(state);
  }

  editProduct(product) {
    this.setState({
      product: { ...product },
      productDialog: true,
    });
  }

  confirmDeleteProduct(product) {
    this.setState({
      product,
      deleteProductDialog: true,
    });
  }

  deleteProduct() {
    let products = this.state.products.filter(
      (val) => val.billno !== this.state.product.billno
    );
    this.setState({
      products,
      deleteProductDialog: false,
      product: this.emptyProduct,
    });
    this.toast.show({
      severity: "success",
      summary: "Successful",
      detail: "Product Deleted",
      life: 3000,
    });
  }

  findIndexById(billno) {
    let index = -1;
    for (let i = 0; i < this.state.products.length; i++) {
      if (this.state.products[i].billno === billno) {
        index = i;
        break;
      }
    }

    return index;
  }
  exportPdf() {
    import("jspdf").then((jsPDF) => {
      import("jspdf-autotable").then(() => {
        const doc = new jsPDF.default(0, 0);
        doc.autoTable(this.exportColumns, this.state.products);
        doc.save("products.pdf");
      });
    });
  }
  exportCSV() {
    this.dt.exportCSV();
  }

  deleteSelectedProducts() {
    let products = this.state.products.filter(
      (val) => !this.state.selectedProducts.includes(val)
    );
    this.setState({
      products,
      deleteProductsDialog: false,
      selectedProducts: null,
    });
    this.toast.show({
      severity: "success",
      summary: "Successful",
      detail: "Products Deleted",
      life: 3000,
    });
  }

  onCategoryChange(e) {
    let product = { ...this.state.product };
    product["category"] = e.value;
    this.setState({ product });
  }

  onInputChange(e, name) {
    const val = (e.target && e.target.value) || "";
    let product = { ...this.state.product };
    product[`${name}`] = val;

    this.setState({ product });
  }

  onInputNumberChange(e, name) {
    const val = e.value || 0;
    let product = { ...this.state.product };
    product[`${name}`] = val;

    this.setState({ product });
  }

  rightToolbarTemplate() {
    return (
      <React.Fragment>
        <Button
          label="Submit"
          type="button"
          onClick={this.printjson.bind(this)}
          className="p-button-primary p-mr-2"
        ></Button>
        <Button
          label="Export PDF"
          type="button"
          icon="pi pi-file-pdf"
          onClick={this.exportPdf}
          className="p-button-help p-mr-2"
          data-pr-tooltip="PDF"
        />
        <Button
          label="Export Excel"
          icon="pi pi-upload"
          className="p-button-success p-mr-2"
          onClick={this.exportCSV}
          data-pr-tooltip="PDF"
        />
      </React.Fragment>
    );
  }

  priceBodyTemplate(rowData) {
    return this.formatCurrency(rowData.price);
  }

  actionBodyTemplate(rowData) {
    return (
      <React.Fragment>
        <Button
          icon="pi pi-pencil"
          className="p-button-rounded p-button-success p-mr-2"
          onClick={() => this.editProduct(rowData)}
        />
        <Button
          icon="pi pi-trash"
          className="p-button-rounded p-button-warning"
          onClick={() => this.confirmDeleteProduct(rowData)}
        />
      </React.Fragment>
    );
  }
  // Greeting() {
  //   const isLoggedIn = this.props.items;
  //   if (isLoggedIn.length == 0) {
  //     return this.state.products;
  //   }
  //   return this.props.items;
  // }

  render() {
    const header = (
      <div className="table-header">
        <span className="p-input-icon-left">
          <i className="pi pi-search" />
          <InputText
            type="search"
            onInput={(e) => this.setState({ globalFilter: e.target.value })}
            placeholder="Search..."
          />
        </span>
      </div>
    );
    const productDialogFooter = (
      <React.Fragment>
        <Button
          label="Cancel"
          icon="pi pi-times"
          className="p-button-text"
          onClick={this.hideDialog}
        />
        <Button
          label="Save"
          icon="pi pi-check"
          className="p-button-text"
          onClick={this.saveProduct}
        />
      </React.Fragment>
    );
    const deleteProductDialogFooter = (
      <React.Fragment>
        <Button
          label="No"
          icon="pi pi-times"
          className="p-button-text"
          onClick={this.hideDeleteProductDialog}
        />
        <Button
          label="Yes"
          icon="pi pi-check"
          className="p-button-text"
          onClick={this.deleteProduct}
        />
      </React.Fragment>
    );
    const deleteProductsDialogFooter = (
      <React.Fragment>
        <Button
          label="No"
          icon="pi pi-times"
          className="p-button-text"
          onClick={this.hideDeleteProductsDialog}
        />
        <Button
          label="Yes"
          icon="pi pi-check"
          className="p-button-text"
          onClick={this.deleteSelectedProducts}
        />
      </React.Fragment>
    );

    return (
      <div className="datatable-crud-demo">
        <Toast ref={(el) => (this.toast = el)} />

        <div className="card">
          <DataTable
            ref={(el) => (this.dt = el)}
            value={this.state.products}
            dataKey="id"
            paginator
            rows={10}
            rowsPerPageOptions={[5, 10, 25]}
            paginatorTemplate="FirstPageLink PrevPageLink PageLinks NextPageLink LastPageLink CurrentPageReport RowsPerPageDropdown"
            currentPageReportTemplate="Showing {first} to {last} of {totalRecords} products"
            globalFilter={this.state.globalFilter}
            header={header}
          >
            <Column field="billno" header="Bill Number" sortable></Column>
            <Column field="category" header="Category" sortable></Column>
            <Column field="weight" header="Weight" sortable></Column>
            <Column field="location" header="Location" sortable></Column>
            <Column
              field="price"
              header="Price"
              body={this.priceBodyTemplate}
              sortable
            ></Column>
            <Column field="tdate" header="Date" sortable></Column>

            <Column body={this.actionBodyTemplate}></Column>
          </DataTable>
          <Toolbar
            className="p-mb-4"
            right={this.rightToolbarTemplate}
          ></Toolbar>
        </div>

        <Dialog
          visible={this.state.productDialog}
          style={{ width: "450px" }}
          header="Product Details"
          modal
          className="p-fluid"
          footer={productDialogFooter}
          onHide={this.hideDialog}
        >
          <div className="p-field">
            <label htmlFor="billno">Bill Number</label>
            <InputText
              id="billno"
              value={this.state.product.billno}
              onChange={(e) => this.onInputChange(e, "billno")}
              required
              autoFocus
              className={classNames({
                "p-invalid": this.state.submitted && !this.state.product.billno,
              })}
            />
            {this.state.submitted && !this.state.product.billno && (
              <small className="p-error">Bill Number is required.</small>
            )}
          </div>

          <div className="p-field">
            <label className="p-mb-3">Category</label>
            <div className="p-formgrid p-grid">
              <div className="p-field-radiobutton p-col-6">
                <RadioButton
                  inputId="category1"
                  name="category"
                  value="AS"
                  onChange={this.onCategoryChange}
                  checked={this.state.product.category === "AS"}
                />
                <label htmlFor="category1">AS</label>
              </div>
              <div className="p-field-radiobutton p-col-6">
                <RadioButton
                  inputId="category2"
                  name="category"
                  value="ES"
                  onChange={this.onCategoryChange}
                  checked={this.state.product.category === "ES"}
                />
                <label htmlFor="category2">ES</label>
              </div>
              <div className="p-field-radiobutton p-col-6">
                <RadioButton
                  inputId="category3"
                  name="category"
                  value="DS"
                  onChange={this.onCategoryChange}
                  checked={this.state.product.category === "DS"}
                />
                <label htmlFor="category3">DS</label>
              </div>
            </div>
          </div>

          <div className="p-formgrid p-grid">
            <div className="p-field p-col">
              <label htmlFor="weight">Weight</label>
              <InputNumber
                id="weight"
                value={this.state.product.weight}
                onValueChange={(e) => this.onInputNumberChange(e, "weight")}
              />
            </div>
            <div className="p-field p-col">
              <label htmlFor="location">Location</label>
              <InputText
                id="location"
                value={this.state.product.location}
                onChange={(e) => this.onInputChange(e, "location")}
              />
            </div>
          </div>

          <div className="p-formgrid p-grid">
            <div className="p-field p-col">
              <label htmlFor="price">Price</label>
              <InputNumber
                id="price"
                value={this.state.product.price}
                onValueChange={(e) => this.onInputNumberChange(e, "price")}
                mode="currency"
                currency="INR"
                locale="en-US"
              />
            </div>
            <div className="p-field p-col">
              <label htmlFor="tdate">Date</label>
              <Calendar
                id="tdate"
                value={this.state.product.tdate}
                onValueChange={(e) => this.onInputNumberChange(e, "tdate")}
              />
            </div>
          </div>
        </Dialog>

        <Dialog
          visible={this.state.deleteProductDialog}
          style={{ width: "450px" }}
          header="Confirm"
          modal
          footer={deleteProductDialogFooter}
          onHide={this.hideDeleteProductDialog}
        >
          <div className="confirmation-content">
            <i
              className="pi pi-exclamation-triangle p-mr-3"
              style={{ fontSize: "2rem" }}
            />
            {this.state.product && (
              <span>
                Are you sure you want to delete entry will Bill Number
                <b>{this.state.product.billno}</b>?
              </span>
            )}
          </div>
        </Dialog>
      </div>
    );
  }
}

export default SearchTable;
