import React, { Component } from "react";
import { InputText } from "primereact/inputtext";
import { Calendar } from "primereact/calendar";
import { Button } from "primereact/button";

class InputForm extends React.Component {

  render() {
    return (
      <div id="Form">
        <h3>Add a new item to the table:</h3>
        <form onSubmit={this.props.handleFormSubmit}>
          <div>
            <div className="card">
              <div className="p-grid p-fluid">
                <div className="p-col-12 p-md-4">
                  <div className="p-inputgroup">
                    <span className="p-inputgroup-addon">
                      <i className="pi pi-user"></i>
                    </span>
                    <InputText
                      placeholder="Bill Number"
                      value={this.props.inputBillNo}
                      name="billno"
                      onBlur={this.props.handleInputChange}
                    />
                  </div>
                </div>

                <div className="p-col-12 p-md-4">
                  <div className="p-inputgroup">
                    <span className="p-inputgroup-addon">
                      <i className="pi pi-envelope"></i>
                    </span>
                    <InputText
                      placeholder="Category"
                      value={this.props.inputCategory}
                      disabled
                      name="category"
                    />
                  </div>
                </div>

                <div className="p-col-12 p-md-4">
                  <div className="p-inputgroup">
                    <span className="p-inputgroup-addon">
                      <i className="pi pi-inbox"></i>
                    </span>
                    <InputText
                      placeholder="Weight"
                      value={this.props.inputWeight}
                      name="weight"
                      onChange={this.props.handleInputChange}
                      required
                    />
                  </div>
                </div>

                <div className="p-col-12 p-md-4">
                  <div className="p-inputgroup">
                    <span className="p-inputgroup-addon">
                      <i className="pi pi-globe"></i>
                    </span>
                    <InputText
                      placeholder="Location"
                      value={this.props.inputLocation}
                      name="location"
                      onChange={this.props.handleInputChange}
                      required
                    />
                  </div>
                </div>

                <div className="p-col-12 p-md-4">
                  <div className="p-inputgroup">
                    <span className="p-inputgroup-addon">Rs.</span>
                    <InputText
                      placeholder="Price"
                      value={this.props.inputPrice}
                      name="price"
                      onChange={this.props.handleInputChange}
                      required
                    />
                  </div>
                </div>

                <div className="p-col-12 p-md-4">
                  <div className="p-inputgroup">
                    <span className="p-inputgroup-addon">
                      <i className="pi pi-calendar"></i>
                    </span>
                    <Calendar
                      dateFormat="dd/mm/yy"
                      placeholder="Date"
                      value={this.props.inputDate}
                      onChange={this.props.handleDate}
                    ></Calendar>
                  </div>
                </div>
              </div>
            </div>
            <Button label="Add Item" value="Submit" />
          </div>
        </form>
      </div>
    );
  }
}

export default InputForm;
