import React, { Component } from "react";
import BaseTable from "./BaseTable";
import { Calendar } from "primereact/calendar";
import { Button } from "primereact/button";
import { Messages } from "primereact/messages";
import { Message } from "primereact/message";
import { Toast } from "primereact/toast";
import axios from "axios";
import SearchMonthlyTable from "./SearchMonthyTable";

class MonthView extends Component {
  constructor() {
    super();

    this.state = {
      monthdate: "",
      daydate: "",
      varll: [],
      headers: [
        { label: "Bill Number", key: "CONSIGNMENT_NUMBER" },
        { label: "Bill Name", key: "CATEGORY" },
        { label: "Weight", key: "PARCEL_WEIGHT" },
        { label: "Location", key: "PLACE" },
        { label: "Price", key: "PARCEL_RATE" },
        { label: "Date", key: "BOOKING_DATE" },
      ],
    };
  }

  handleMonthSubmit = (e) => {
    e.preventDefault();
    this.messages.show({
      severity: "success",
      summary: "Request Submitted Successfully For the Month ",
      detail: String(this.state.monthdate),
    });
    let k = { month: this.state.monthdate };
    const requestOptions = {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
        Accept: "application/json",
      },
      body: JSON.stringify(k),
    };
    fetch("http://127.0.0.1:5000/get_monthly_data", requestOptions)
      .then((response) => response.json())
      .then((data) => {
        this.setState({ varll: data.data });
        console.log(this.state.varll);
      });
  };
  handleDaySubmit = (e) => {
    e.preventDefault();
    this.messages.show({
      severity: "success",
      summary: "Request Submitted Successfully For Date ",
      detail: String(this.state.daydate),
    });
    let k = { month: this.state.daydate };
    const requestOptions = {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
        Accept: "application/json",
      },
      body: JSON.stringify(k),
    };
    fetch("http://127.0.0.1:5000/get_daily_data", requestOptions)
      .then((response) => response.json())
      .then((data) => {
        this.setState({ varll: data.data });
        console.log(this.state.varll);
      });
  };
  sendMonth = (e) => {
    let ISTTime = e.target.value;
    let day = ISTTime.getDate();
    let month = ISTTime.getMonth() + 1;
    let year = ISTTime.getFullYear();
    let newdate = year + "-" + month + "-" + day;
    this.state.monthdate = newdate;
  };
  sendDay = (e) => {
    let ISTTime = e.target.value;
    let day = ISTTime.getDate();
    let month = ISTTime.getMonth() + 1;
    let year = ISTTime.getFullYear();
    let newdate = year + "-" + month + "-" + day;
    this.state.daydate = newdate;
  };

  render() {
    return (
      <div>
        <form>
          <div>
            <h2></h2>
            <div className="card">
              <div className="p-grid p-fluid">
                <div className="p-col-12 p-md-4">
                  <div className="p-inputgroup">
                    <span className="p-inputgroup-addon">
                      <i className="pi pi-calendar"></i>
                    </span>
                    <Calendar
                      value={this.state.daydate}
                      onChange={(e) => this.sendMonth(e)}
                      view="month"
                      dateFormat="MM-yy"
                    />
                  </div>
                </div>

                <div className="p-col-12 p-md-4">
                  <div className="p-inputgroup">
                    <Button
                      label="Get Monthly"
                      value="Submit"
                      onClick={(e) => {
                        this.handleMonthSubmit(e);
                      }}
                    />
                  </div>
                </div>
              </div>
            </div>
            <div className="card">
              <div className="p-grid p-fluid">
                <div className="p-col-12 p-md-4">
                  <div className="p-inputgroup">
                    <span className="p-inputgroup-addon">
                      <i className="pi pi-calendar"></i>
                    </span>
                    <Calendar
                      value={this.state.daydate}
                      onChange={(e) => this.sendDay(e)}
                    />
                  </div>
                </div>

                <div className="p-col-12 p-md-4">
                  <div className="p-inputgroup">
                    <Button
                      label="Get By Date"
                      value="Submit"
                      onClick={(e) => {
                        this.handleDaySubmit(e);
                      }}
                    />
                  </div>
                </div>
              </div>
            </div>
          </div>
          {/* <Messages ref={(el) => this.messages = el}></Messages> */}
          <Toast ref={(el) => (this.messages = el)} />
          <SearchMonthlyTable
            dataFromParent={JSON.stringify(this.state.varll)}
          ></SearchMonthlyTable>
        </form>
      </div>
    );
  }
}

export default MonthView;
