import "primeicons/primeicons.css";
import "primereact/resources/themes/saga-blue/theme.css";
import "primereact/resources/primereact.css";
import "primeflex/primeflex.css";
import "../index.css";

import { Menubar } from "primereact/menubar";
import { InputText } from "primereact/inputtext";
import React, { Component } from "react";
import {
  BrowserRouter as Router,
  Redirect,
  Route,
  Link,
} from "react-router-dom";
import HomeForm from "./HomeForm";
import MonthView from "./MonthView";
import UploadFile from "./UploadFile";
import SearchTable from "./SearchTable";
import { Button } from "primereact/button";
import { RadioButton } from "primereact/radiobutton";
import { InputNumber } from "primereact/inputnumber";
import { Calendar } from "primereact/calendar";
import { Dialog } from "primereact/dialog";
import { classNames } from "primereact/utils";

class Navbar extends Component {
  constructor(props) {
    super(props);
    this.state = {
      query: "",
      result: [],
      filteredResult: [],
    };
    this.items = [
      {
        label: "Home",
        icon: "pi pi-fw pi-home",
        command: () => {
          window.location = "bluedart-frontend/home";
        },
      },
      {
        label: "Month",
        icon: "pi pi-fw pi-calendar",
        command: () => {
          window.location = "/month";
        },
      },
      {
        label: "Analytics",
        icon: "pi pi-fw pi-cog",
        command: () => {
          window.location = "/upload";
        },
      },
    ];
    this.editProduct = this.editProduct.bind(this);
    this.getData = this.getData.bind(this);
  }
  handleQuery = (e) => {
    let input = e.target.value;
    this.setState({
      query: input,
    });
  };
  getData() {
    console.log(this.state.query);
    const requestOptions = {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify(this.state.query),
    };
    fetch("http://127.0.0.1:5000/search_data", requestOptions)
      .then((response) => response.json())
      .then((data) => {
        this.setState({ result: data.data });
        console.log(this.state.result);
      });
    this.editProduct(this.state.result);
  }
  editProduct(product) {
    this.setState({
      product: { ...product },
      productDialog: true,
    });
  }
  render() {
    const end = (
      <div>
        <InputText
          placeholder="Search"
          value={this.state.query}
          onChange={this.handleQuery}
          type="text"
        />
        <Button
          icon="pi pi-search"
          className="p-button-primary"
          onClick={this.getData}
        ></Button>
      </div>
    );
    const productDialogFooter = (
      <React.Fragment>
        <Button
          label="Cancel"
          icon="pi pi-times"
          className="p-button-text"
          onClick={this.hideDialog}
        />
        <Button
          label="Save"
          icon="pi pi-check"
          className="p-button-text"
          onClick={this.saveProduct}
        />
      </React.Fragment>
    );
    const deleteProductDialogFooter = (
      <React.Fragment>
        <Button
          label="No"
          icon="pi pi-times"
          className="p-button-text"
          onClick={this.hideDeleteProductDialog}
        />
        <Button
          label="Yes"
          icon="pi pi-check"
          className="p-button-text"
          onClick={this.deleteProduct}
        />
      </React.Fragment>
    );
    const deleteProductsDialogFooter = (
      <React.Fragment>
        <Button
          label="No"
          icon="pi pi-times"
          className="p-button-text"
          onClick={this.hideDeleteProductsDialog}
        />
        <Button
          label="Yes"
          icon="pi pi-check"
          className="p-button-text"
          onClick={this.deleteSelectedProducts}
        />
      </React.Fragment>
    );

    return (
      <div>
        <div className="card">
          <Menubar model={this.items} end={end} />
          <Router basename={process.env.PUBLIC_URL}>
            <div>
              <Route exact path="/" render={() => <Redirect to="/home" />} />
              <Route path="/home" component={HomeForm} />
              <Route path="/month" component={MonthView} />
              <Route path="/upload" component={UploadFile} />
              <Route path="/search" component={SearchTable} />
            </div>
          </Router>
        </div>
        <Dialog
          visible={this.state.productDialog}
          style={{ width: "450px" }}
          header="Product Details"
          modal
          className="p-fluid"
          footer={productDialogFooter}
          onHide={this.hideDialog}
        >
          <div className="p-field">
            <label htmlFor="billno">Bill Number</label>
            <InputText
              id="billno"
              value={this.state.result.billno}
              onChange={(e) => this.onInputChange(e, "billno")}
              required
              autoFocus
              className={classNames({
                "p-invalid": this.state.submitted && !this.state.result.billno,
              })}
            />
            {this.state.submitted && !this.state.result.billno && (
              <small className="p-error">Bill Number is required.</small>
            )}
          </div>

          <div className="p-field">
            <label className="p-mb-3">Category</label>
            <div className="p-formgrid p-grid">
              <div className="p-field-radiobutton p-col-6">
                <RadioButton
                  inputId="category1"
                  name="category"
                  value="AS"
                  onChange={this.onCategoryChange}
                  checked={this.state.result.category === "AS"}
                />
                <label htmlFor="category1">AS</label>
              </div>
              <div className="p-field-radiobutton p-col-6">
                <RadioButton
                  inputId="category2"
                  name="category"
                  value="ES"
                  onChange={this.onCategoryChange}
                  checked={this.state.result.category === "ES"}
                />
                <label htmlFor="category2">ES</label>
              </div>
              <div className="p-field-radiobutton p-col-6">
                <RadioButton
                  inputId="category3"
                  name="category"
                  value="DS"
                  onChange={this.onCategoryChange}
                  checked={this.state.result.category === "DS"}
                />
                <label htmlFor="category3">DS</label>
              </div>
            </div>
          </div>

          <div className="p-formgrid p-grid">
            <div className="p-field p-col">
              <label htmlFor="weight">Weight</label>
              <InputNumber
                id="weight"
                value={this.state.result.weight}
                onValueChange={(e) => this.onInputNumberChange(e, "weight")}
              />
            </div>
            <div className="p-field p-col">
              <label htmlFor="location">Location</label>
              <InputText
                id="location"
                value={this.state.result.location}
                onChange={(e) => this.onInputChange(e, "location")}
              />
            </div>
          </div>

          <div className="p-formgrid p-grid">
            <div className="p-field p-col">
              <label htmlFor="price">Price</label>
              <InputNumber
                id="price"
                value={this.state.result.price}
                onValueChange={(e) => this.onInputNumberChange(e, "price")}
                mode="currency"
                currency="INR"
                locale="en-US"
              />
            </div>
            <div className="p-field p-col">
              <label htmlFor="tdate">Date</label>
              <Calendar
                id="tdate"
                value={this.state.result.tdate}
                onValueChange={(e) => this.onInputNumberChange(e, "tdate")}
              />
            </div>
          </div>
        </Dialog>
      </div>
    );
  }
}

export default Navbar;
